'use client';
import '@/app/globals.css';
import { Inter } from 'next/font/google';
import React, { useEffect } from 'react';
import { LockClosedIcon, MoonIcon, SunIcon } from '@heroicons/react/24/outline';

const inter = Inter({ subsets: ['latin'] });

export default function RootLayout({ children }: { children: React.ReactNode }) {
    const [isDarkMode, setIsDarkMode] = React.useState(false); // Default to light mode

    // Sync dark mode state with document class on mount
    useEffect(() => {
        const isDark = document.documentElement.classList.contains('dark');
        setIsDarkMode(isDark);
    }, []);

    const toggleDarkMode = () => {
        setIsDarkMode((prev) => !prev);
        document.documentElement.classList.toggle('dark');
    };

    return (
        <html lang="en">
        <body className={`${inter.className} bg-gray-100 dark:bg-gray-900 min-h-screen`}>
        {/* Main Content Area */}
        <main className="p-8">
            <div className="max-w-4xl mx-auto">
                {/* Secure Note Branding and Dark Mode Toggle */}
                <div className="flex items-center justify-between mb-4">
                    <div className="flex items-center text-gray-900 dark:text-gray-100 text-2xl font-bold">
                        <LockClosedIcon className="h-6 w-6 mr-2 text-gray-400" />
                        Secure Note
                    </div>
                    <button
                        onClick={toggleDarkMode}
                        className="p-2 rounded-full bg-gray-200 dark:bg-gray-700 text-gray-900 dark:text-gray-100 hover:bg-gray-300 dark:hover:bg-gray-600 transition duration-200"
                    >
                        {isDarkMode ? (
                            <SunIcon className="h-6 w-6" />
                        ) : (
                            <MoonIcon className="h-6 w-6" />
                        )}
                    </button>
                </div>
                {/* Page Content */}
                <div className="bg-gray-50 dark:bg-gray-800 p-8 rounded-lg text-gray-900 dark:text-gray-100">
                    {children}
                </div>
            </div>
        </main>
        </body>
        </html>
    );
}